* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: #fdfeff;
  text-align: center;
  font-weight: lighter;
  font-family: "tajawal";
}

main {
  background: url("./images/cover.png"), linear-gradient(#181818, #0b0b0b);
  background-position: top;
  background-size: 100%;
  background-repeat: no-repeat;
}
.header {
  background-image: url("./images/header.png");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  padding: 200px 0;
  margin: 0 auto;
}
.header h1 {
  font-size: 3em;
  padding-right: 45px;
  padding-top: 15px;
}
@media only screen and (max-width: 460px) {
  .header h1 {
    padding-right: 0;
  }
}
.header p {
  max-width: 900px;
  margin: 0 auto;
  width: 90%;
  font-size: 22px;
}
.welcome h2 {
  font-size: 45px;
}
.welcome p {
  font-size: 24px;
}
.welcome p:first-of-type {
  margin: 10px 0;
}
.main_container {
  max-width: 920px;
  margin: 0 auto;
  margin-top: 40px;
}
.documents {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
label {
  display: flex;
  align-items: top;
  cursor: pointer;
  position: relative;
  width: 49%;
  text-align: right;
  background-color: #36343a;
  padding: 15px 20px;
  padding-left: 5px;
  border-radius: 20px;
  margin: 10px 0;
  font-size: 20px;
}
.checkbox {
  position: relative;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.checkmark {
  position: relative;
  margin-left: 20px;
  top: 50%;
  transform: translateY(-50%);
  height: 25px;
  width: 25px;
  border: 2px #fdfeff solid;
  border-radius: 5px;
}

.checkmark::after {
  content: "";
  position: absolute;
  display: none;
}

label .checkbox:checked ~ .checkmark:after {
  display: block;
}

label .checkmark:after {
  right: 7px;
  top: 2px;
  width: 6px;
  height: 10px;
  border: solid #ffffff;
  border-width: 0 2px 2px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
.red {
  background-color: #c22735;
}
.btns_container {
  display: flex;
  justify-content: space-between;
}
.submit {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 40%;
  max-width: 300px;
  cursor: pointer;
  font-size: 24px;
  outline: none;
  border: none;
  padding: 15px 0em;
  border-radius: 20px;
  background-color: #c22735;
  margin-top: 30px;
}

@media only screen and (max-width: 900px) {
  .main_container {
    width: 90%;
    margin: 3em auto;
    margin-bottom: 0;
  }
  .documents {
    justify-content: center;
  }
  label {
    max-width: 610px;
    width: 100%;
    padding: 25px 20px;
    font-size: 1.7em;
  }
  .welcome {
    max-width: 610px;
    width: 90%;
    margin: 0 auto;
    margin-top: -90px;
    margin-bottom: 120px;
  }
  .welcome h2 {
    font-size: 3em;
  }
  .welcome p {
    font-size: 2em;
  }
  .header {
    padding: 285px 0;
    transform: translateX(25px);
    width: 94%;
  }
  .header h1 {
    font-size: 5em;
  }
  .documents {
    padding: 0 20px;
  }
  .btns_container {
    max-width: 600px;
    width: 100%;
    margin: 30px auto;
  }
  .submit {
    height: 100%;
    padding: 25px 0em;
    max-width: 280px;
    width: 50%;
    font-size: 2em;
  }
}

.btns {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
