.hint,
.answer {
  display: none;
  box-sizing: border-box;
  width: 90%;
  max-width: 650px;
  position: fixed;
  padding: 40px 30px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 20px;
  background-color: #efefef;
}

.hint_header {
  color: #c22735;
  font-weight: bold;
  font-size: 50px;
  margin-bottom: 20px;
}
.hint p {
  color: black;
  font-size: 30px;
  margin-bottom: 30px;
}
.btns {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  cursor: pointer;
  width: 45%;
  margin: 0 10px;
  padding: 20px 0;
  border: none;
  border-radius: 15px;
  outline: none;
  font-size: 30px;
  background-color: #c22735;
}
.hint_btn {
  background-color: #414141;
}

.res_container img {
  width: 100%;
}

.one_right {
  margin: 20px 0;
  font-size: 20px;
  color: #c22735;
  font-weight: bold;
}

.right p {
  color: #414141;
}
.right > p:nth-of-type(1) {
  font-size: 34px;
}
.right > p:nth-of-type(2) {
  margin: 10px 0;
  font-size: 25px;
  color: #00d5aa;
}
.images {
  display: flex;
  justify-content: space-between;
  margin: 10px 0;
}
.images > div {
  flex-basis: 48%;
}
.social_right {
  width: 50%;
  margin: 10px auto;
  display: flex;
  justify-content: space-around;
}
.social_right a {
  width: 20%;
}

@media (max-width: 900px) {
  .btns {
    width: 100%;
    margin: 10px 0;
  }
  .images {
    flex-direction: column;
    padding: 0 40px;
  }
  .images p {
    margin: 20px 0;
  }
  .res_container p {
    font-size: 20px;
  }
}
